import React from 'react';

import './Navbar.css';

class Navbar extends React.Component {

    componentDidMount () {
        window.addEventListener('scroll', () => {

            const isTop = window.scrollY > 100;
            const nav = document.getElementById('navigation');

            if (isTop) {
                nav.classList.add('scrolled');
            } else {
                nav.classList.remove('scrolled');
            }

        });
    }

    componentWillUnmount () {
        window.removeEventListener('scroll');
    }

    render () {
        return (
            <nav id="navigation" className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
                <div className="container">
                    <a id="navbar-header" className="navbar-brand" href="/AingCodeForEducation">A<span>ing</span></a>
                    <button style={{ outline: "none" }} className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item">
                                <a className="nav-link" href="/AingCodeForEducation">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#introduction">Introduction</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/Categories">Categories</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#contact">Contact</a>
                            </li>
                            <li className="nav-item">
                                <div className="g-ytsubscribe" data-channelid="UCUAPb5r4xkJsd8e76HdYgNw" data-layout="default" data-count="default"></div>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }

}

export default Navbar;